<template>

  <div class="gj-bg" style="height:100vh; overflow:hidden">
<Gesture @onSwipeLeft="onSwipeLeft($event)" @onSwipeRight="onSwipeRight($event)">
    <div class="horizontal-scroll-wrapper" ref="main" id="main" style="height:100vh; overflow:hidden">

    <div class="gj-card ml-8" style="overflow:auto" ref="mainDiv">
      <div>
        <a :href="banner.link || '#'">
          <v-card v-if="banner.show" class="mt-14">
            <v-img
              class="white--text align-end"
              height="120px"
              :src="banner.image"
            >
              <!-- <v-card-title>Top 10 Australian beaches</v-card-title> -->
            </v-img>

            <v-card-subtitle class="pb-0" v-if="banner.title">{{banner.title}}</v-card-subtitle>

            <v-card-text class="text--primary">
              <div v-if="banner.subtitle">{{banner.subtitle}}</div>
            </v-card-text>

          </v-card>
        </a>
        <div class="d-flex flex-sm-row flex-column justify-content">
          <div class="mr-8 mt-8">
            <img
                :src="require('../assets/images/agj.png')"
                contain
                height="125"
                width="125"
                class="mb-4 rounded-circle elevation-2"
              />
          </div>
          <div class="d-flex flex-column justify-center d-xl-flex">
            <h1 class="font-weight-bold mb-2 gj-wp-normal" >
              Atelier Grass Jelly
            </h1>
            <p class="gj-wp-normal">
              Crafting is just another way of keeping diary...
            </p>
          </div>
        </div>
      </div>

      <transition-group name="left-slide-fade" tag="div" class="mt-8" v-on:before-enter="listBeforeEnter">
        <a v-for="(data, index) in list" :href="data.href" :key="index" v-bind:data-index="index" class="list-item">
          <v-hover v-slot:default="{ hover }">
            <v-card class="d-flex flex-row justify-content mb-4 pa-4" :elevation="hover ? 8 : 2" v-ripple="{ center: false }" >
              <img
                  class="mr-4"
                  :src="data.image"
                  contain
                  height="35"
                  width="35"
                />
              <h2 class="font-weight-bold" style="color:black">
                {{data.text}}
              </h2>
            </v-card>
          </v-hover>
        </a>
      </transition-group>

    </div>

    <div class="pl-8 pr-8 pt-8 gj-card" style="width: 100%; height:100vh; overflow:auto" ref="photoDiv" v-if="images.length > 0">
      <v-row>

          <v-col class="col-12 col-sm-4">
            <v-row>
              <v-col class="col-6 col-sm-12">
                <CustomImage :image="images[0].image" :url="images[0].url || '#'"></CustomImage>
              </v-col>
            
              <v-col class="col-6 col-sm-12">
                <CustomImage :image="images[1].image" :url="images[1].url || '#'"></CustomImage>
              </v-col>
            </v-row>

          </v-col>
          
          <v-col class="col-12 col-sm-8" >
             <v-row>
              <v-col class="" >
                <CustomImage :image="images[2].image" :url="images[2].url || '#'"></CustomImage>
              </v-col>
             </v-row>
          </v-col>

          <v-col :cols="8" class="" >
             <v-row>
              <v-col class="" >
                <CustomImage :image="images[3].image" :url="images[3].url || '#'"></CustomImage>
              </v-col>
             </v-row>
          </v-col>
          
          <v-col :cols="4">
            <v-row>
              <v-col>
                <CustomImage :image="images[4].image" :url="images[4].url || '#'"></CustomImage>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <CustomImage :image="images[5].image" :url="images[5].url || '#'"></CustomImage>
              </v-col>
            </v-row>
          </v-col>

          <!-- <v-col :cols="4">
            <v-row>
              <v-col>
                <CustomImage url="https://via.placeholder.com/150"></CustomImage>
              </v-col> 
            </v-row>
          </v-col>

          <v-col :cols="4">
            <v-row>
              <v-col>
                <CustomImage url="https://via.placeholder.com/150"></CustomImage>
              </v-col>
            </v-row>
          </v-col> -->
          
      </v-row>
    </div>

    </div>
</Gesture>

    <div class="fab-button">
      <v-fab-transition>
                <v-btn
                  color="lime"
                  dark
                  absolute
                  right
                  fab
                  style="bottom:16px"
                  @click="scrollTo"
                >
                  <v-icon color="black">{{ fabIcon }}</v-icon>
                </v-btn>
      </v-fab-transition>
    </div>
    
  </div>
</template>

<script>
import CustomImage from './CustomImage';
import Gesture from 'euv-gesture'
const axios = require('axios');

  export default {
    name: 'About',

    components: {
      CustomImage,
      Gesture
    },

    data: () => ({
      show: true,
      hover: false,
      list: [],
      images: [],
      banner: {},
      fabIcon: "mdi-chevron-right",
    }),
    created: function(){
    },
    mounted: function () {
      this.setData();
    },
    methods: {
      setData(){

        axios.get('https://ateliergrassjelly.github.io/resource/json/links-v2.json')
          .then(response => {
            this.list = response.data.links;
            this.banner = response.data.highlights;
          });

        axios.get('https://ateliergrassjelly.github.io/resource/json/images.json')
          .then(response => {
            this.images = response.data
          });

      },
      scrollTo(){
        if(this.fabIcon == "mdi-chevron-left"){
          this.scrollToMain();
        }else{
          this.scrollToPhoto();
        }
      },
      scrollToPhoto(){
          this.$refs.photoDiv.scrollIntoView({behavior: "smooth", inline: "center", block:"start"});
          this.fabIcon = "mdi-chevron-left"
      },
      scrollToMain(){
          this.$refs.mainDiv.scrollIntoView({behavior: "smooth", inline: "center", block:"start"});
          this.fabIcon = "mdi-chevron-right"
      },
      listBeforeEnter: function (el) {
        var delay = (el.dataset.index) * 0.2;
        el.style.transitionDelay = delay+'s';
      },
      onSwipeLeft() {
        var v = this;
        setTimeout(function () {
          v.scrollToPhoto();
        }, 100);
      },
      onSwipeRight() {
        var v = this;
        setTimeout(function () {
          v.scrollToMain();
        }, 100);
      }
    }
  }
</script>
